import './App.css';
import { useState } from 'react'

function App() {
  const [ ans , setAns ] = useState([])  
  const [ inputNum , setInputNum ] = useState('')
  const [ start , setStart ] = useState('開始')
  const [ num , setNum ] = useState([])
  const [ prompt ,setPrompt ] = useState('')

  const getRandom = () =>{
    return Math.floor(Math.random()*10)
  }
  const handlestart = () =>{
    var i
    var a=[]
    var s=[]
      for(i=0;i<4;i++){
        a = getRandom()
        if(s.indexOf(a)<0){
          s.push(a);
        }else{
          i = i-1; 
        }
      }
      setAns(s)
      setStart('重新')
      setPrompt('')
  }
  const handleNumber = (e) =>{
    if(ans.length>1){
      if(inputNum.length<4){
        setInputNum(inputNum+e.target.dataset.number)
        num[num.length]=Number(e.target.dataset.number)
        setNum(num)
      }
    }
  }
  const handleInput = () =>{
    var a=0
    var b=0
    for(var i=0 ; i<4 ; i++){
      var idx = ans.indexOf(num[i])
      if(idx !=-1){
        if (idx == i){
          a++
        }else{
          b++
        }
      }
    }
    if(a==4){
      alert('win')
    }
    setPrompt(num+ '　' +a+'A'+b+'B'+'　'+prompt)
    setNum([])
    setInputNum([])
  }

  const handleclear = () =>{
    setInputNum([])
    setNum([])
  }
  console.log(num);
  console.log("input", inputNum);
  console.log("ans", ans);

  return (
    <div className="App">
      <div className="title">猜數字</div>
      <div className="wrapper">
        <div className="ansbar">{prompt}</div>
        <div className="action-wrapper">
          <button className="start" onClick={handlestart}>{start}</button>
          <button className="reset" onClick={handleclear}>清除</button>
        </div>
        <div className="input-wrapper">
          <div className="enter-wrapper">
            <div className="input-bar">{inputNum}</div>
            <button className="enter-btn" onClick={handleInput}>輸入</button>
          </div>
          <div className="num-btn-wrapper">
            <button className="num-btn" onClick={handleNumber} data-number="1">1</button>
            <button className="num-btn" onClick={handleNumber} data-number="2">2</button>
            <button className="num-btn" onClick={handleNumber} data-number="3">3</button>
            <button className="num-btn" onClick={handleNumber} data-number="4">4</button>
            <button className="num-btn" onClick={handleNumber} data-number="5">5</button>
            <button className="num-btn" onClick={handleNumber} data-number="6">6</button>
            <button className="num-btn" onClick={handleNumber} data-number="7">7</button>
            <button className="num-btn" onClick={handleNumber} data-number="8">8</button>
            <button className="num-btn" onClick={handleNumber} data-number="9">9</button>
            <button className="num-btn" onClick={handleNumber} data-number="0">0</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
